import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useTheme } from '@mui/material/styles';
import { styled } from '@mui/material/styles';

import { Grid, Table, TableBody, TableCell, Stack, tableCellClasses, Card, IconButton, Menu, MenuItem, CardContent, Button, TextField, TableContainer, TableHead, TableRow, Tooltip, Fab } from '@mui/material';
// project imports
import { useDispatch, useSelector } from 'store';
//Icons
import DeleteIcon from '@mui/icons-material/Delete'
// project imports
import AnimateButton from 'ui-component/extended/AnimateButton';

// third-party
import { useFormik } from 'formik';
import EditAttributesSharp from '@mui/icons-material/Edit';

//redux
import { SaveInventoryData } from 'store/slices/inventoryteam';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        maxWidth: 100
    }
}));

export default function PaymentForm({ paymentData, handleNext, handleBack }) {

    const dispatch = useDispatch();

    const formik = useFormik({
        initialValues: {
            cardName: paymentData.cardName,
            cardNumber: paymentData.cardNumber
        },
        onSubmit: (values) => {
            SaveTableData();
            handleNext();
        }
    });
    const theme = useTheme();

    const inventorydataState = useSelector((state) => state.inventory);

    const [AllDataList, setAllDataList] = useState([]);
    const [editid, setEditId] = useState("");
    useEffect(() => {
        const filterdata = inventorydataState.inventorydata.filter((item) => {
            return Object.values(item).some((value) => value !== "");
        })
        setAllDataList(filterdata);
    }, [inventorydataState]);

    const handleInputChange = (id, column, value) => {
        const updatedData = AllDataList.map((row) =>
            row.id === id ? { ...row, [column]: value } : row
        );
        setAllDataList(updatedData);
    };

    const EditData = (id) => () => {
        setEditId(id);
    }

    const DeleteRowData = (id) => () => {
        setAllDataList(AllDataList.filter((row) => row.id != id));
    }

    const SaveTableData = async () => {
        await dispatch(SaveInventoryData(AllDataList));
    }

    return (
        <>
            <form onSubmit={formik.handleSubmit}>
                <Grid container spacing={3}>
                    <Grid item xs={12}>
                        <TableContainer>
                            <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                                <TableHead>
                                    <TableRow>
                                        <StyledTableCell align='center'>Cost / sqared meter(3cm)</StyledTableCell>
                                        <StyledTableCell align='center'>Cost / sqared meter(2cm)</StyledTableCell>
                                        <StyledTableCell align='center'>Cost / cubic meter</StyledTableCell>
                                        <StyledTableCell align='center'>total / block heatment cost</StyledTableCell>
                                        <TableCell align='center'>Epoxy gel cost</TableCell>
                                        <StyledTableCell align='center'>Quantity of lboxv gel</StyledTableCell>
                                        <StyledTableCell align='center'>Ihermal lboxv gel</StyledTableCell>
                                        <TableCell align='center'>termal warp</TableCell>
                                        <TableCell align='center'>Block Dimensions</TableCell>
                                        <TableCell align='center'>Sawing Date</TableCell>
                                        <TableCell align='center'>Material</TableCell>
                                        <TableCell align='center'>Block Number</TableCell>
                                        <TableCell align='center'>Action</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {AllDataList.map((row) => (
                                        <>
                                            {row.id != editid && (
                                                <TableRow hover key={row.id}>
                                                    {Object.keys(row).map((item, i) => (
                                                        <>
                                                            {item != "id" && (
                                                                <TableCell align="center" key={i}>
                                                                    <TableCell align="center" key={i}>{row[item]}</TableCell>
                                                                </TableCell>
                                                            )}
                                                        </>
                                                    ))}
                                                    <TableCell align="center" sx={{ pr: 3 }}>
                                                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                                                            <EditAttributesSharp
                                                                onClick={EditData(row.id)}
                                                                fontSize="small"
                                                                aria-controls="menu-popular-card-1"
                                                                aria-haspopup="true"
                                                                sx={{ color: 'grey.500' }}
                                                            />
                                                            <DeleteIcon
                                                                onClick={DeleteRowData(row.id)}
                                                                fontSize="small"
                                                                aria-controls="menu-popular-card-1"
                                                                aria-haspopup="true"
                                                                sx={{ color: 'grey.500', ml: 2 }}
                                                            />
                                                        </div>
                                                    </TableCell>
                                                </TableRow>
                                            )}
                                            {editid == row.id && (
                                                <TableRow hover key={row.id}>
                                                    {Object.keys(row).map((item, i) => (
                                                        <>
                                                            {item != "id" && (
                                                                <TableCell align="center" key={i}>
                                                                    <TextField
                                                                        value={row[item]}
                                                                        onChange={(e) => handleInputChange(row.id, item, e.target.value)}
                                                                        id="standard-basic" variant="standard"
                                                                    />
                                                                </TableCell>
                                                            )}
                                                        </>
                                                    ))}
                                                    <TableCell align="center" sx={{ pr: 3 }}>
                                                        <IconButton onClick={DeleteRowData(row.id)} size="large">
                                                            <DeleteIcon
                                                                fontSize="small"
                                                                aria-controls="menu-popular-card-1"
                                                                aria-haspopup="true"
                                                                sx={{ color: 'grey.500' }}
                                                            />
                                                        </IconButton>
                                                    </TableCell>
                                                </TableRow>
                                            )}
                                        </>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack direction="row" justifyContent="space-between">
                            <Button onClick={handleBack} sx={{ my: 3, ml: 1 }}>
                                Back
                            </Button>
                            <AnimateButton>
                                <Button variant="contained" type="submit" sx={{ my: 3, ml: 1 }}>
                                    Next
                                </Button>
                            </AnimateButton>
                        </Stack>
                    </Grid>
                </Grid>
            </form>
        </>
    );
}

PaymentForm.propTypes = {
    paymentData: PropTypes.object,
    setPaymentData: PropTypes.func,
    handleNext: PropTypes.func,
    handleBack: PropTypes.func,
    setErrorIndex: PropTypes.func
};
